:root {
  --dark-blue: hsl(233, 26%, 24%);
  --lime-green: hsl(136, 65%, 51%);
  --bright-cyan: hsl(192, 70%, 51%);
  --grayish-blue: hsl(233, 8%, 62%);
  --light-grayish-blue: hsl(220, 16%, 96%);
  --very-light-gray: hsl(0, 0%, 98%);
  --white: hsl(0, 0%, 100%);
  --btn-bg-color: linear-gradient(to right, hsl(136, 65%, 51%), hsl(192, 70%, 51%));
}

.navbar > .container-fluid {
  padding: 0;
}

.nav-item {
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: var(--white);
}

.nav-item:hover {
  border-bottom-color: var(--lime-green);
}

.navbar-toggler {
  border-style: none;
}

.navbar-toggler:focus,
.navbar-toggler:active,
.navbar-toggler-icon:focus {
  outline: none;
  box-shadow: none;
}

.custom-btn {
  display: inline-block;
  text-decoration: none;
  font-weight: bold;
  color: var(--white);
  padding: 10px 30px;
  border-radius: 30px;
  border-style: none;
  background-image: var(--btn-bg-color);
  white-space: nowrap;
}

.custom-btn:focus,
.custom-btn:active,
.custom-btn:focus {
  outline: none;
  box-shadow: none;
}

.custom-btn:hover {
  opacity: 70%;
}

.top-section-right-lg {
  background-image: url("/src/assets/images/bg-intro-desktop.svg");
  background-repeat: no-repeat;
  background-position: top -255px right 80%;
  background-size: 130% 950px;
  position: relative;
}

.top-section-right-sm {
  background-image: url("/src/assets/images/bg-intro-mobile.svg");
  background-position: center;
  background-size: 100% auto;
  text-align: center;
  order: -1;
}

.why-choose-card {
  width: 23%;
  max-width: 500px;
  min-width: 300px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.article-card {
  width: 23%;
  max-width: 600px;
  min-width: 300px;
  margin-top: 20px;
  margin-right: 10px;
  border-style: none;
}

h1 {
  font-weight: 400;
  color: var(--dark-blue);
  font-size: 2rem;
}

h6 {
  font-weight: 400;
  color: var(--grayish-blue);
  font-size: 12px;
}

p {
  color: var(--grayish-blue);
}

.heading {
  font-size: 3rem;
}

.feature {
  font-size: 1.5rem;
}

h5:hover {
  color: var(--lime-green);
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--white);
}

a:hover {
  color: var(--lime-green);
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  padding-bottom: 10px;
}

i.fab {
  margin-right: 3px;
}

i.fab:hover {
  color: var(--lime-green);
}

body {
  background-color: var(--white);
}

.bg-very-light-grey {
  background-color: var(--very-light-gray);
}

.bg-light-grayish-blue {
  background-color: var(--light-grayish-blue);
}

.box-spacing {
  padding-top: 100px !important;
  padding-bottom: 100px !important;
}

footer {
  background-color: var(--dark-blue);
  color: var(--white);
  padding-top: 50px;
  padding-bottom: 50px;
}